.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 280px;

  &__image {
    border-radius: 50%;
    width: 150px;
    margin-bottom: 24px;
    
    @include onDesktop {
      transition: transform, box-shadow 0.5s, 0.5s linear;
      cursor: zoom-in;

      &:hover {
        box-shadow: 0 5px 20px 0 rgba(0, 83, 181, 0.46);
        transform: scale(1.2);
      }
    }
  }

  &__name {
    margin: 0;
    margin-bottom: 4px;
    color: $colorBlue;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    text-decoration: none;

    @include onDesktop {
      transition: transform 0.5s linear;

      &:hover {
        transform: scale(1.2);
      }
    }
  }

  &__position {
    margin: 0;
    margin-bottom: 4px;
    font-size: 14px;
    line-height: 27px;
    color: #9fadc2;
  }

  &__description {
    margin: 0;
    font-size: 16px;
    line-height: 27px;
    text-align: center;
  }
}
